            <div class="amount with-buttons flex-item" :style="'margin-top: 16px;'">
                <AmountViewStaking :title="$localizeFilter('balance_usdt') + ' \\'" :amount="balance" :type="'balance'"  :closestPayoutTimestamp="closestPayoutTimestamp" @showWithdrawOptions="showWithdrawOptions" @hideWithdrawOptions="showWithdrawDropdown=false"/>   
                <div class="withdraw-options-flex">
                    <WithdrawOptions class="withdraw-options" v-if="showWithdrawDropdown" @selectOption='selectWithdrawOption' @close="showWithdrawDropdown=false"/>
                </div>
            </div>

            <div class="amount flex-item" :style="'margin-top: 16px;'">
                <AmountViewSimpleStaking class="amount-view" :title="$localizeFilter('amount_income_coin', ['CWPT']) + ' \\'" :amount="currentTotalIncome" :type="'income'"/>
            </div>


        </div>

        <div class="table-container">
            <StakingOperations ref="operations" class="operations" :type="'bonusAccount'" :coin="'USDT'" :showOptions="true"/>
