        <StakingBalanceDepositFromExternalWallet v-if="showDepositFromExternalWalletModal" @closeModal="closeModals"/> 

        <BurnCWPTForFENIXModal v-if="showBurnCWPTforFENIXModal" @closeModal="closeModals" :rateCWPTToFENIX="rateCWPTToFENIX" :rateCWPTToUSDT="rateCWPTToUSDT"  :staked="staked"/>

        <InfoModal v-if="showInfoModal" :title="$localizeFilter('staking_info_modal_title')" :view="'full'" :text="$localizeFilter('staking_info_modal_text')" :button="{title: $localizeFilter('staking_info_modal_button'), action: infoModalAction}" @closeModal="closeModals"  />
